<template>
  <VueHtml2pdf
    ref="html2Pdf"
    :show-layout="false"
    :float-layout="true"
    :enable-download="false"
    :preview-modal="true"
    :paginate-elements-by-height="3508"
    :filename="$route.params.id"
    :pdf-quality="2"
    :manual-pagination="false"
    pdf-format="a4"
    pdf-orientation="portrait"
    pdf-content-width="800px"
    class="h-full"
    @hasDownloaded="dowloading"
  >
    <section slot="pdf-content" class="mt-0 p-16 text-black">
      <!-- PDF Content Here -->

      <img
        src="@//assets//images//fondo.jpg"
        style="width: 500px;"
        class=" opacity-20 mt-64 ml-24 fixed items-center text-center w-auto "
      />

      <section class="flex justify-between">
        <img src="@//assets//images//unap-full.jpg" class=" w-40" />
        <div class=" flex flex-col text-right text-lg uppercase">
          <span>Dirección de Admision</span>
          <div class=" flex flex-col font-bold">             
            <span>(DA)</span>
          </div>
        </div>
      </section>
      <!--  -->

      <div class=" font-bold uppercase text-center text-3xl mt-20 font-sans">
        <h1>constancia de ingreso</h1>
      </div>

      <section class="mt-20 text-lg w-full text-justify">
        <p>
          La jefa de la <b>Dirección de Admisión</b> que suscribe, hace
          constar que
          <b class=" uppercase">
            {{
              `${reportData.apellido_paterno} ${
                reportData.apellido_materno
              } ${trimStr(reportData.nombre)}`
            }}</b
          >, ingresó a la carrera profresional de
          <b class=" uppercase">{{ reportData.escuela }}</b
          >, en la modalidad <b>{{ reportData.modalidad }}</b
          >, con codigo de postulante <b>N° {{ reportData.dni }}</b
          >, sumando un total de
          <b>{{ parseMoney(reportData.nota_examen) }}</b> puntos en el
          <b>Examen de Admisión 2021 - II</b>, lo que le confiere el derecho a
          cubrir una de las vacantes ofertadas por esta carrera profresional.
        </p>

        <p class="mt-10">
          Al haber cumplido con la presentación de los documentos requeridos por
          el Reglamento de Admisión, se le otorga la presente Constancia,
          declarandole expedito para la inclusión en la Resolución Rectoral
          respectiva, que oficializa, en ultima instancia, su condición de
          ingresante a la Universidad Nacional de la Amazonía Peruana.
        </p>

        <div class=" text-right mt-10">
          <span class=" text-right">
            Iquitos, 20 de Abril de 2021
          </span>
        </div>

        <div
          class=" text-center m-auto mt-10 border-t border-dashed w-64 flex flex-col"
        >
          <span>Dra. Maritza Grandez Ruiz</span>
          <span>Jefa de DA</span>
        </div>
      </section>

      <div class=" flex justify-between mt-16">
        <span></span>
        <img src="@//assets//images//licenciada.png" class=" w-52" />
      </div>
    </section>
  </VueHtml2pdf>
</template>

<script>
import axios from "@//plugins//axios";
const VueHtml2pdf = () => import("vue-html2pdf");

export default {
  components: {
    VueHtml2pdf,
  },

  data() {
    return {
      reportData: {},
    };
  },

  mounted() {
    this.getReport();
  },

  methods: {
    dowloading() {
      // this.cerrarPestania();
    },

    trimStr(val) {
      if (!val) return;
      return val.trim();
    },

    parseMoney(val) {
      const formatterPeso = new Intl.NumberFormat("es-PE", {
        currency: "PEN",
        minimumFractionDigits: 4,
      });
      val = Math.round(val * 100) / 100;
      return formatterPeso.format(val);
    },

    cerrarPestania() {
      window.open("", "_parent", "");
      window.close();
    },

    async getReport() {
      try {
        const { id } = this.$route.params;
        if (!id) return;

        const { data } = await axios.get("get_declaracion_jurada_by_id", {
          params: { id },
        });

        this.reportData = data[0];
        this.$refs.html2Pdf.generatePdf();
        if (!this.reportData.cubrio_vacante) {
          this.cerrarPestania();
          return;
        }
      } catch (error) {
        console.error(error);
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
